* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

html {
    scroll-behavior: smooth;
    //     background-color: #000000;
    //     color: white;
}

.App {
    // scroll-behavior: smooth;
    background-color: #000000;
    color: white;

    .Logo {
        position: absolute;
        top: 0;
        z-index: 1;
        background-color: black;
        box-shadow: 0px 23px 44px 22px rgba(0, 0, 0, 1);

        @media(orientation: landscape) {
            // background-color: #fff;
            width: 100%;
        }

        img {
            height: 100%;
            object-fit: contain;
            width: 100%;

            @media(orientation: landscape) {
                height: 15vh;
            }
        }
    }

    .MainPhoto {
        height: 100vh;
        width: 100%;
        overflow: hidden;

        .simpleParallax {
            height: 100%;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .ButtonsContainer {
        position: absolute;
        // background-color: yellowgreen;
        width: 220px;
        height: 250px;
        right: 20px;
        bottom: 20px;

        &__btn {
            // position: absolute;
            right: 0;
            padding: 3px 15px;
            color: white;
            border: none;
            font-size: 1.4em;
            // text-transform: uppercase;
            // font-weight: 900;
            border: 2px solid white;
            transition: .2s;
            background-color: black;
            text-align: center;
            margin-bottom: 8px;
            // backdrop-filter: blur(4px);
        }

        // &__btnGallery {
        //     top: 50px;
        // }

        // &__btnOffer {
        //     top: 100px;
        // }

        // &__btnCard {
        //     top: 150px;
        // }

        // &__btnContact {
        //     top: 200px;
        // }
    }

    .txt {
        padding: 35px 20px;

        h1 {
            text-align: center;
            padding-bottom: 15px;
            text-transform: uppercase;
            font-weight: 900;
            font-size: 2em;
        }
    }

    .photo {
        img {
            object-fit: cover;
            width: 100vw;
            height: 50vh;
        }
    }

    .Card {
        background-color: #fff;

        &__drink {
            transition: .5s;
            transform: translate(-30%);
            opacity: 0;
            padding: 20px 20px;

            &--visible {
                opacity: 1;
                transform: translate(0);
            }

            &:nth-child(even) {
                background-color: #fff44f;
                // text-align: right;
            }
        }

        h2 {
            color: rgb(90, 90, 90);
            font-family: 'Titillium Web', sans-serif;
            font-weight: 900;
            text-transform: uppercase;
            font-size: 1.8em;
            letter-spacing: 1px;
        }

        p {
            color: rgb(92, 92, 92);
            margin-bottom: 0;
        }
    }

    .Contact {
        p {
            margin: 0;
        }

        a {
            color: white;
            text-decoration: none;
        }
    }

    .Footer {
        background-color: white;
        text-align: center;
        color: rgb(92, 92, 92);
        margin: 0;
        padding-top: 16px;
    }

    .lemonYellow {
        color: #fff44f;
    }
}